.forms {
  height: 100%;
}
.forms .formsContainer {
  height: 100%;
}
.forms .refresh {
  display: flex;
  padding: 0 10px 0 0;
  align-items: center;
  justify-content: space-between;
}
.forms .refresh .pageTitle {
  flex: 0.98 1;
  min-width: auto;
}
.forms .refresh .fa {
  cursor: pointer;
}
.forms .ant-form {
  overflow: auto;
  max-height: calc(100% - 60px);
}
.forms .ant-form .ant-form-item-children .ant-btn-primary {
  margin-right: 1rem;
}
.forms .react-json-view {
  overflow: auto;
  max-height: calc(100% - 60px);
}
